.math-container {
    font-family: Arial, sans-serif;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .math-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .math-table th,
  .math-table td {
    padding: 8px;
    border: 1px solid #ddd;
  }
  
  .math-table th {
    background-color: #f2f2f2;
  }
  
  .math-table tr:last-child {
    font-weight: bold;
    background-color: #ffc107;
  }
  