/* components/ViewCustomers.css */
.view-customers {
    margin: 0;
  }
  
  .view-customers h2 {
    font-size: 24px;
    color: #333;
  }
  
  .view-customers table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .view-customers th, .view-customers td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .view-customers th {
    background-color: #f2f2f2;
  }
  
  .view-customers tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .view-customers tr:hover {
    background-color: #ddd;
  }
  