/* NewBooking.css */

.booking-container {
    max-width: 600px;
    margin: 0 ;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

  }
  
  .heading {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .customer-section,
  .hall-section,
  .date-time-section,
  .package-section,
  .package-details,
  .custom-package,
  .food-section,
  .soft-drink-section,
  .hard-drink-section {
    margin-bottom: 20px;
  }
  
  .labelhead {
    display: flex;
    justify-content: center; /* Center the text horizontally */
    align-items: center; /* Center the text vertically */
    padding: 6px;
    margin: 0;
    background-color: #7c8288;
    color: #fbfbfb;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 0px;
}

  
  .label {
    display: flex;
    padding: 6px;
    margin: 0;
    background-color: #d6dadd;
    color: #222121;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  
  .input,
  .select {
    width: 97%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-top: -8px;
  }
  
  .button,
  .submit-button {
    padding: 7px 14px;
    background-color: #1d6dc3;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 4px;
  }
  
  .button:hover,
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  .checkbox-list {
    list-style: none;
    padding: 0;
  }
  
  .checkbox-item {
    margin-bottom: 10px;
  }
  
  .checkbox-label {
    display: inline-block;
    margin-left: 5px;
  }
  
  .checkbox {
    margin-right: 5px;
  }

  
  /* Styles for the customer table */
.customer-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.customer-table th,
.customer-table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

.customer-table th {
  background-color: #f2f2f2;
}

.customer-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.checkbox-calculate{
  display: inline-block;
}
.checkbox-container{
  background-color: #d6dadd;
  padding: 5px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.total-cost-section {
  border: 2px solid #7c8288;
  padding: 10px;
  margin-top: 20px;
  background-color: #b7b3b3;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}

.price-calculation-container {
  display: flex;
  align-items: center;
}

/* Adjust margin or padding as needed */
.calculate-button{
  padding: 4px 8px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
}

.calculate-button:hover {
  background-color: #0056b3;
}

.price-per-person-section {
  margin-right: 10px; /* Add margin between the button and the input field */
}
