.view-bookings-container {
    max-width: 800px;
    margin: 0 ;
    padding: 20px;
  }
  
  .calendar {
    display: flex;
    flex-wrap: wrap;
  }
  
  .calendar-nav {
    margin-bottom: 20px;
  }
  
  .calendar-grid {
    display: flex;
    flex-direction: column;
  }
  
  .calendar-day {
    margin-bottom: 10px; /* Reduce the margin between days */
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
  .calendar-day h3 {
    margin-bottom: 8px;
    font-size: 15px;
  }
  
  
  .booking-section {
    margin-bottom: 20px;
  }
  
  .booking-section h4 {
    margin-bottom: 10px;
    font-size: 18px;
    color: #333;
  }
  
  .booking-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .booking-table td {
    padding: 10px;
    border: 1px solid #ccc;
  }
  
  .booking-table td:first-child {
    width: 150px;
    font-weight: bold;
    background-color: #f2f2f2;
  }
  
  .booking-table td:last-child {
    padding-left: 10px;
  }
  
  .booking-table tbody tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .booking-table tbody tr:hover {
    background-color: #e6f7ff;
  }
  

 
 .mbutton{
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 20px;
    margin-bottom: 10px;
  }
  
  .mbutton:hover {
    background-color: #0056b3;
  }

  .booking-popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }
  
  .booking-popup h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .booking-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .booking-table td, .booking-table th {
    padding: 8px;
    border: 1px solid #ddd;
  }
  
  .booking-table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .booking-table tr:hover {
    background-color: #ddd;
  }
  
  .booking-table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #205eda;
    color: white;
  }
  